.site-title {
  @include relative-font-size(1.625);
  &,
  &:visited {
    color: $primary-text-color;
  }
}

.site-description {
  @include relative-font-size(0.875);
  color: $secondary-text-color;
}

.pp {
  width: 4rem;
  border-radius: 50%;
}

.site-info {
  display:flex;
  flex-direction: column;  
}

.meta-header {
  padding-top: 1.5rem;
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 1px solid lighten($secondary-text-color, 35%);
  padding-bottom: $spacing-unit/2.0;
}

.site-nav {
  margin-top: $spacing-unit/2; 
  margin-bottom: $spacing-unit;
  font-size: 1rem;
  display: flex;
  flex-direction: row;
  
  > * {
      position: relative;
      margin-right: $spacing-unit/2;
      text-align:center;            
  }

  > a:hover {    
    text-decoration: none;    
  }

  > .active {
      color: $tint-color;        
  }
  
  > .active::after {
      content:'';
      position:absolute;
      width:50%;
      height:2px;
      background: $tint-color;
      bottom:0;
      left:0;
      right:0;
      margin:auto;
  }
}

/**
 * Page content
 */

.page-heading {
  @include relative-font-size(2);
}

.post-list-heading {
  @include relative-font-size(1.75);
}

.post {
  display: flex;
  flex-direction: column;
  margin-bottom: 1.5rem;
}

.post-meta {  
  text-transform: uppercase;   
  color: $secondary-text-color;

  @include relative-font-size(0.75);
}

.post-link {  
  @include relative-font-size(1.3);
}

.post-description {
  color: $secondary-text-color;
  @include relative-font-size(0.875);
}

/**
 * Posts
 */
.post-header {
  margin-bottom: $spacing-unit/2;
}

.post-title,
.post-content h1 {
  @include relative-font-size(2);
  letter-spacing: -1px;
  line-height: 1.15;

  @media screen and (min-width: $on-large) {
    @include relative-font-size(2);
  }
}

.post-content {
  margin-bottom: $spacing-unit/2;

  h2, h3 { margin-top: $spacing-unit * 2 }
  h4, h5, h6 { margin-top: $spacing-unit }

  h2 {
    @include relative-font-size(1.75);

    @media screen and (min-width: $on-large) {
      @include relative-font-size(2);
    }
  }

  h3 {
    @include relative-font-size(1.375);

    @media screen and (min-width: $on-large) {
      @include relative-font-size(1.625);
    }
  }

  h4 {
    @include relative-font-size(1.25);
  }

  h5 {
    @include relative-font-size(1.125);
  }
  h6 {
    @include relative-font-size(1.0625);
  }
}

/**
 * Pagination navbar
 */
.pagination {
  margin-bottom: $spacing-unit;
  @extend .social-media-list;
  li {
    a, div {
      min-width: 41px;
      text-align: center;
      box-sizing: border-box;
    }
    div {
      display: block;
      padding: $spacing-unit / 4;
      border: 1px solid transparent;

      &.pager-edge {
        color: $secondary-text-color;
        border: 1px dashed;
      }
    }
  }
}

.align-center {
  display: flex;
  justify-content: center;
  margin: 16px 0 16px 0;
}

/**
 * Grid helpers
 */
@media screen and (min-width: $on-large) {
  .one-half {
    width: calc(50% - (#{$spacing-unit} / 2));
  }
}
